import React, { PureComponent } from "react";
import "animate.css/animate.min.css";
import { Form as AntForm, Modal, Input } from 'antd';

class Form extends PureComponent {
  state = { error: false };
  input = ({ target: { value, name } }) => this.setState({ [name]: value });
  submitData = async () => {
    if (!this.handleValidation()) return
    let body = this.state;
    body = JSON.stringify({ ...this.state, platform: "Webinar" });
    console.log(body)
    const data = await fetch("https://q18668pxed.execute-api.us-east-1.amazonaws.com/working", {
      method: "POST",
      headers: {
        "Content-type": "application/json"
      },
      body,
      //mode: 'no-cors'
    });
    const json = await data.text();
    console.log(json);
    this.setState({ done: true })
    Modal.success({
      title: "Éxito",
      content: "Datos enviados correctamente",
      onOk: ()=>{
        window.location.href = "http://35.227.121.90/#enviado"
      }
    })
  };
 

  handleValidation() {
    let fields = this.state;
    let formIsValid = true;

    //Name
    if (!fields["name"]) {
      formIsValid = false;
    }

    if (typeof fields["name"] !== "undefined") {
      if (!fields["name"].match(/^[a-züéáíóúñ]+$/gi)) {
        formIsValid = false;
      }
    }


    //Email
    if (!fields["email"]) {
      formIsValid = false;
    }

    if (typeof fields["email"] !== "undefined") {
      let lastAtPos = fields["email"].lastIndexOf("@");
      let lastDotPos = fields["email"].lastIndexOf(".");

      if (
        !(
          lastAtPos < lastDotPos &&
          lastAtPos > 0 &&
          fields["email"].indexOf("@@") == -1 &&
          lastDotPos > 2 &&
          fields["email"].length - lastDotPos > 2
        )
      ) {
        formIsValid = false;
      }
    }

    this.setState({ error: !formIsValid });
    return formIsValid;
  }



  render() {
    return (
      <div className="div-block-24">
        <div className="div-block-26">
          <div className="div-block-30">
            <p className="paragraph-12 responseT">
              We want to be in touch with you
             </p>
            <p className="paragraph-13">
              subscribe
             </p>
          </div>
        </div>

        <div className="div-block-27">
          <div className="div-block-28">
            <div class="">
              <AntForm className="div-block-29">
                <div id="form" className="topBefore">
                  <div className="form white">
                    <Input
                      id="name"
                      name="name"
                      type="text"
                      value={this.state.name}
                      onChange={this.input}
                      placeholder="Your name"
                      className="mg-input"
                      requiered
                    />
                    <Input
                      id="email"
                      name="email"
                      className=""
                      value={this.state.email}
                      onChange={this.input}
                      type="text"
                      placeholder="Email"
                      requiered
                    />
                  </div>
                  <div className="form white">
                    <Input
                      id="cargo"
                      name="cargo"
                      type="text"
                      value={this.state.cargo}
                      onChange={this.input}
                      placeholder="cargo"
                      className="mg-input"
                      requiered
                    />
                    <Input
                      id="company"
                      name="company"
                      className=""
                      value={this.state.company}
                      onChange={this.input}
                      type="text"
                      placeholder="company"
                      requiered
                    />
                  </div>
                  <br />
                  <div className="btndiv">
                    <input onClick={this.submitData} type="submit"
                    value="Enviar" className="btn-2 _2 btn4"/>
                  </div>
                </div>
              </AntForm>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Form;
